@font-face {
  font-family: 'MontserratarmRegular';
  src: url('../public/assets/Fonts/Montserratarm-Regular.otf');
}
@font-face {
  font-family: 'MontserratarmBold';
  src: url('../public/assets/Fonts/Montserratarm-Bold.otf');
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'MontserratarmRegular', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'MontserratarmBold', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

